import tuiSticky from "@/pages/components/sticky/sticky"
import { miniProApi } from '@/utils/mixins.js'	
export default {
	mixins:[
		miniProApi
	],
	components:{
		tuiSticky
	},
	data() {
		return {
			scrollTop: 0
		}
	},
	computed:{
		scrollContentHeight(){
			return (this.pHeight - 113)
		},
		stickyTop(){



			

			return 110

		}
	},
	methods: {
		detail(e) {
			this.tui.toast("详情功能尚未完善~")
		},
		scrollViewScroll(e){
			// debugger
			console.log(e.detail)
			this.scrollTop= e.detail.scrollTop
		},
	},
	onLoad() {
		this.$on("change", (data) => {
			console.log("--------", data)
			
		})
	},
	//页面滚动执行方式
	onPageScroll(e) {
		this.scrollTop= e.scrollTop
		console.log(this.scrollTop)
	}
}
